.main {
  top: 150px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main__image {
  border-radius: 50%;
  width: 250px;
  height: 250px;
}
.icon {
  padding-top: 70px;
  padding: 10px;
  width: 50px;
}

.bio {
  max-width: 700px;
  font-size: 25px;
  font-family: "inter";
}
.bio > strong {
  color: #2b9ef1;
  font-weight: bold;
}
.download__icon {
  width: 35px;
  height: 35px;
}

.download__button {
  border: none;
  background: orange;
  font-family: inter;
  font-weight: bold;
  font-size: 33px;
  padding: 5px;
  cursor: pointer;
  color: inherit;
  text-decoration: inherit;
}
.signature {
  margin: 40px;
}
